




























































































import { Component, Vue, Watch } from "vue-property-decorator";
import MarkDown from "./components/markDown.vue";
import { apiUrl } from "../../api";
import Uploads from "@/components/uploads/upload.vue";
@Component({
    name: "articleAdd",
    components: {
        MarkDown,
        Uploads
    }
})
export default class ArticleAdd extends Vue {
    private categoryList = [];
    created() {
        let articleData = this.$route.params.articleData;
        if (articleData) {
            (this as any).ruleForm = articleData;
        } else {
            this.$router.push("/article/list");
        }

        (this as any).$axios.get(`${apiUrl.tagList}`).then((res: any) => {
            if (res.data.code === 0) {
                this.categoryList = res.data.message;
                console.log(res);
            }
        });
    }
    private ruleForm = {
        title: "",
        top: "",
        category_id: "",
        keyword: "",
        thumb_url: "",
        type: [],
        content: "",
        desc: "",
        article_author: "",
        createdAt: "",
        count: 0,
        content_mark: ""
    };
    @Watch("ruleForm.content")
    changDesc(val) {
        console.log(val);
        this.ruleForm.desc = val
            .toString()
            .replace(/<.*?>/gi, "")
            .replace(/[\r\n]/g, "")
            .substring(0, 200);
    }
    private rules = {
        title: [
            { required: true, message: "请输入标题", trigger: "blur" },
            {
                min: 3,
                max: 50,
                message: "长度在 3 到 50 个字符",
                trigger: "blur"
            }
        ],
        count: [
            {
                type: "number",
                required: true,
                message: "请输入数字",
                trigger: "blur"
            }
        ],

        category_id: [
            {
                required: true,
                message: "请选择一个栏目",
                trigger: "change"
            }
        ],
        content: [{ required: true, message: "请输入内容", trigger: "change" }]
    };
    private changValue(text: string) {
        this.ruleForm.content = text;
    }
    private changMark(text: string) {
        this.ruleForm.content_mark = text;
    }
    private submitForm(formName: string) {
        var that = this;
        (this.$refs[formName] as any).validate((valid: boolean) => {
            if (valid) {
                console.log(this.ruleForm);
                (this as any).$axios
                    .post(`${apiUrl.articleUpdate}`, this.ruleForm)
                    .then((res: any) => {
                        if (res.data.code == 0) {
                            this.$message.success("更新成功");
                            (that.$router as any)
                                .push("/article/list")(this as any)
                                .resetForm();
                        }
                    });
            } else {
                console.log("error submit!!");
                return false;
            }
        });
    }
    private resetForm(formName: string) {
        (this.$refs[formName] as any).resetFields();
    }
    private changeUrl(img: string) {
        this.ruleForm.thumb_url = img;
    }
    private removeUrl() {
        this.ruleForm.thumb_url = "";
    }
}
